@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,500;1,500&family=Fraunces:ital,wght@0,300;0,400;1,300&family=IBM+Plex+Sans:wght@400;700&family=Inter:wght@500;600;800&display=swap");
/* Fix IE Only */
.video {
    box-shadow: 0px 9px 5px 1px rgba(0, 0, 0, 0.31);
}

#index {
    position: relative;
    top: -180px;
    z-index: 0;
}

#index p {
    font-size: small;
}

#index .history {
    background-color: #ffffff;
}

#index .history h2 {
    color: #b5a780;
}

@media screen and (max-width: 576px) {
    #index .history h2 {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

#index .history .button-history {
    background-color: #b5a780;
    text-decoration: none;
    color: #000000;
    height: 50px;
    border-radius: 100px;
    align-items: center;
}

#index .history .button-history:hover {
    background-color: #454B5B;
    text-decoration: none;
    color: #000000;
}

#index .tourism {
    background-color: #b5a780;
}

#index .tourism h2 {
    color: #000000;
}

#index .tourism .button-tourism {
    background-color: #ffffff;
    text-decoration: none;
    height: 50px;
    border-radius: 100px;
    color: #000000;
}

#index .tourism .button-tourism:hover {
    background-color: #454B5B;
    text-decoration: none;
    color: #000000;
}

#index .tourism .vertical {
    border-left: 2px solid #000000;
    height: 75px;
    display: inline-block;
}

#index .photos {
    background-color: #ffffff;
}

#index .photos h2 {
    color: #b5a780;
}

#index .photos .button-photos {
    background-color: #454B5B;
    text-decoration: none;
    height: 50px;
    border-radius: 100px;
    color: #000000;
}

#index .photos .button-photos:hover {
    background-color: #b5a780;
    text-decoration: none;
    color: #000000;
}

#index .photos .masonry-galery {
    columns: 3 330px;
    column-gap: 0.5rem;
    row-gap: 0.5rem;
}

@media screen and (max-width: 992px) {
    #index .photos .masonry-galery {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

#index .photos .masonry-galery div {
    width: 390px;
    background: none;
    display: inline-block;
    text-align: center;
    font-family: system-ui;
    font-weight: 900;
    font-size: 2rem;
    background-size: cover;
    transition: 0.4s;
}

#index .photos .masonry-galery div img {
    object-fit: cover;
    height: 100%;
    width: 95%;
}

#index .photos .masonry-galery .item-1 {
    height: 443px;
}

#index .photos .masonry-galery .item-2 {
    height: 356px;
}

#index .photos .masonry-galery .item-3 {
    height: 472px;
}

#index .photos .masonry-galery .item-4 {
    height: 265px;
}

#index .photos .masonry-galery .item-5 {
    height: 585px;
}

#index .photos .masonry-galery .item-6 {
    height: 236px;
}

#index .photos .masonry-galery .photobloc {
    position: relative;
}

.events-carousel h2 {
    color: #b5a780;
}

.events-carousel .carousel-item {
    height: 500px !important;
}

.navbar {
    position: relative;
    z-index: 1;
    background-color: #00000067;
    color: #ffffff;
}

.navbar a {
    text-decoration: none;
    color: #ffffff;
}

.navbar a:hover {
    color: #b5a780;
}

.navbar-brand img {
    width: 100px;
}

.nav-link {
    font-size: 25px;
}

header {
    position: relative;
    top: -216px;
    z-index: 0;
}

footer {
    background-color: #b5a780;
    height: 300px;
    font-size: 40px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: #000000;
}

@media screen and (max-width: 992px) {
    footer {
        flex-direction: column;
    }
}

footer img {
    width: auto;
    height: 150px;
}

footer ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer ul li {
    list-style-type: none;
}

footer ul li .fab:hover {
    color: #ffffff;
}